.form {
  width: 600px; }
  .form textarea {
    width: 360px;
    height: 80px; }

.form-error {
  display: none;
  position: absolute;
  margin-top: 30px;
  margin-left: 16px;
  background: red;
  padding: 0.3rem;
  font-size: 80%;
  box-shadow: 0 1px 4px -1px black; }

.form-body > * {
  display: table;
  margin-bottom: 1rem; }

.form-body > * > * {
  display: table-cell;
  vertical-align: middle; }

.form-body > * > *:first-child {
  width: 180px;
  padding-right: 1rem;
  text-align: right; }
