html, body, #app {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: #f2f2f2; }

#app-header {
  display: table;
  width: 100%;
  height: 60px;
  background: #d9d9d9;
  border-bottom: 1px solid #cccccc; }
  #app-header > div {
    max-height: 60px; }

#app-header > div {
  display: table-cell;
  vertical-align: middle;
  padding-left: 1rem;
  padding-right: 1rem; }

#app-body {
  display: table;
  width: 100%;
  height: calc( 100vh - 100px); }

#app-body > div {
  display: table-cell;
  vertical-align: top; }

#app-sidebar {
  width: 25%;
  max-width: 200px; }

#app-footer {
  display: table;
  width: 100%;
  height: 40px;
  background: #d9d9d9;
  border-top: 1px solid #cccccc; }
  #app-footer > div {
    max-height: 40px; }

#app-footer > div {
  display: table-cell;
  vertical-align: middle;
  padding-left: 1rem;
  padding-right: 1rem; }

@media screen and (max-height: 400px) {
  #app-footer {
    display: none; } }
