.menu-vertical {
  list-style: none; }

.menu-vertical > * {
  display: block; }

.menu-vertical > *:first-child > *:first-child {
  border-top: 1px solid #cccccc; }

.menu-vertical > * > * {
  display: block;
  padding: 0.5rem 1rem;
  border-bottom: 1px solid #cccccc;
  text-decoration: none;
  user-select: none; }

.menu-vertical > * > *:hover {
  color: #333333;
  background: #d9d9d9;
  text-decoration: none; }

.menu-vertical > * > *:active {
  color: #404040;
  background: #cccccc; }
